import React, {useEffect, useState} from 'react'
import axios from 'axios'
import ReactDOM from 'react-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPenToSquare, faTrash } from '@fortawesome/free-solid-svg-icons'
import { EditForm } from '../components/Forms/FormsDashboard'
import * as moment from 'moment';
import 'moment/locale/es';

import './dashboard.css'
import '../components/Forms/formsDashboard.css'


export default function Dashboard() {
    let tkn;
    const [navState, setNavState] = useState()
    const [itemAction, setItemAction] = useState()
    const [formState, setFormState] = useState(false)
    const [panelNot, setPanelNot] = useState(false)
    
    const formatDate = (date) => {
        var date = moment(date);
        return date.format('LL')
    }
    
    const AgendItem = () => {
        return(
            <div className='agend-item'>
                                            <div className='item-header'>
                                                <div className='header-icons'>
                                                    <FontAwesomeIcon onClick={()=>{setItemAction('editar'); setFormState(true)}} title='Editar' className='icon' icon={faPenToSquare} />
                                                    <FontAwesomeIcon onClick={()=>{setPanelNot(true); setItemAction('borrar');}} title='Borrar' className='icon'  icon={faTrash} />
                                                    
                                                </div>
                                                <p>11 de Septiembre</p>
                                                <p>Santa Fe, Argentina</p>
                                            </div>
                                            <div className='item-body'>
                                                <h2>Title</h2>
                                                <p>Parrafo de texto para prueba parrafo de texto para prueba parrafo de texto
                                                     para prueba parrafo de texto para prueba</p>
                                            </div>
            </div>
        )
    }
    useEffect(()=>{
        tkn = sessionStorage.getItem('tkn')
        console.log('nuevos cambios')

        setNavState('agenda')
        axios.get('https://api.escuelademusicabarrial.ar/api/dashboard/', {
            headers: {
                'auth-token':`${tkn}`
            }
        })
        .then((res)=>{
            if(res.status === 200){

                console.log(res.data)
                console.log('acceso concedido')
                console.log(tkn)
            }
            else {
                console.log('acceso denegado')
            }
        })
       
        
    }, [])
    return(
        <div className='dashboard-admin'>
            <div className='dashboard-header'>
                <h1>Panel administrador</h1>

            </div>
            <div className='dashboard-container'>
                <div className='dashboard-sidebar'>
                    <li>
                        <a className='active' onClick={()=>{ setNavState('agenda') }}>
                            Agenda
                        </a>
                    </li>
                     
                </div>
                <div className='dashboard-panel'>
                    <EditForm  state={formState} setForm={setFormState}/>
                    {
                        panelNot===true&&itemAction==='borrar'?
                        <div className='panel-not'>
                            {
                                itemAction==='borrar'?
                                <p>{'¿Queres eliminar esta publicación?'}</p> : ''
                                
                            }
                            <div>
                                <button onClick={ ()=>{
                                    setPanelNot(false); setItemAction(undefined)
                                } } className='btn btn-dark'>Aceptar</button>
                                <button onClick={ ()=>{
                                    setPanelNot(false); setItemAction(undefined)
                                } } className='btn btn-dark'>Cancelar</button>
                            </div>
                        </div> : 
                        panelNot===false? <></> : <></>
                    }
                    {
                        navState === 'agenda' ? 
                        <div className='agend-panel'>
                            <div className='agend-header'>
                                <h2>Agenda de novedades</h2>
                            </div>
                            <div className='agend-list'>
                                <AgendItem />
                                <AgendItem />
                                <AgendItem />
                                <AgendItem />
                                <AgendItem />
                                <AgendItem />
                            </div>                                
                        </div> : ''
                        
                        
                    }
                </div>
            </div>
        </div>
    )
}
