import React, {useState, useRef, useEffect} from 'react'; 
import { Formik } from 'formik';
import './formsDashboard.css'

export const EditForm = ({state, setForm}) => {

    const [formDate, setFormDate] = useState()
    const dateInput = useRef()
    const form = useRef()

    useEffect(()=>{
        
    }, [])

    
    
    
    
    return(
        <Formik
        
            initialValues={{ title: '', text: '',
                            date: '', ubication: '', ubicationLink: '', }}
            validate={() => {
                const errors = {};
               
                return errors;
            }}
            onSubmit={(values, { setSubmitting }) => {
                setTimeout(() => {
             
                    form.current.className='d-none'
                
                setSubmitting(false);
                }, 400);
            }}
            >
            {({
                values,
                errors,
                touched,
                handleChange,
                handleBlur,
                handleSubmit,
                isSubmitting,
                /* and other goodies */
            }) => (
                <form ref={form} className={state==false?'d-none':
                                            state==true?'edit-form d-block' : ''} onSubmit={handleSubmit}>
                    <div>
                        <div className=''>
                            <input
                                type="title"
                                name="title"
                                placeholder='Titulo'
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.title}
                            />
                            {errors.title && touched.title && errors.title}
                            <input
                                type="text"
                                name="text"
                                placeholder='Texto del artículo'
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.text}
                            />
                            {errors.text && touched.text && errors.text}

                        </div>
                        <div className=''>
                            <input 
                                type="date" 
                                ref={dateInput}
                                name="date"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                min="2020-01-01" 
                                max="2024-12-31"
                                value={values.date}
                            />
                            {errors.date && touched.date && errors.date}
                            <input
                                type="ubication"
                                name="ubication"
                                placeholder='Ubicación'
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.ubication}
                            />
                            {errors.ubication && touched.ubication && errors.ubication}
                           
                            <input
                                type="ubicationLink"
                                name="ubicationLink"
                                placeholder='Link de la ubicación'
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.ubicationLink}
                            />
                            {errors.ubication && touched.ubication && errors.ubication}
                        </div>

                    </div>
                <button onClick={()=>{setForm(false)}} className='btn btn-dark' type="submit" disabled={isSubmitting}>
                    Agregar
                </button>
                </form>
            )}
            </Formik>
    )
}